.card{
    border-radius: 20px;
    background-color: #c6d8dda1;
    box-shadow: 0px 0px 4px 2px rgba(0,0,0,0.25);
}

.book-btn-color{
    color:white;
    background-color:#FF4A52;
}


.card-design:hover{
    transform: scale(1.05);
    transition: ease-in   0.5s;
    transition: ease-out  0.5s;
}